const smsLogin = resolve => require(['@/components/TheSmsLogin.vue'], resolve)
const pwdLogin = resolve => require(['@/components/ThePwdLogin.vue'], resolve)
const register = resolve => require(['@/components/TheRegister.vue'], resolve)

export default [
  {
    path: 'smsLogin',
    name: 'SmsLogin',
    component: smsLogin,
    meta: {
      title: '短信登录'
    }
  },
  {
    path: 'pwdLogin',
    name: 'PwdLogin',
    component: pwdLogin,
    meta: {
      title: '密码登录'
    }
  },
  {
    path: 'register',
    name: 'Register',
    component: register,
    meta: {
      title: '注册登录'
    }
  }
]
