import axios from 'axios'
import { BASE_URL } from './config'

const $http = axios.create({
  baseURL: BASE_URL,
  headers: { 'Content-Type': 'application/json;charset=UTF-8' },
  // headers: {
  //             "X-Requested-With": "XMLHttpRequest",
  //             // "X-Csrf-Token": this.insuredForm.csrfToken
  //           }
  timeout: 15000
})

