export default {
  get (url, params = {}) {
    return new Promise((resolve, reject) => {
      $http.get(url, params, {}).then(res => {
        resolve(res.data)
      }).catch(error => {
        if (error.response) {
          resolve(error.response.data)
        }
      })
    })
  },
  post (url, params = {}) {
    // params = formatData(params)
    return new Promise((resolve, reject) => {
      $http.post(url, params, {}).then(res => {
        resolve(res.data)
      }).catch(error => {
        if (error.response) {
          resolve(error.response.data)
        }
      })
    })
  }
}
