import Vue from 'vue'
import Router from 'vue-router'

import loginRegister from './login-register'
import product from './product'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/home',
      name: 'home',
      component: resolve => require(['@/views/Home.vue'], resolve),
      meta: {
        title: '首页'
      }
    },
    {
      path: '/login',
      name: 'login',
      component: resolve => require(['@/views/AccountLogin.vue'], resolve),
      meta: {
        title: '登录'
      }
    },
    {
      path: '/personCenter',
      name: 'personCenter',
      redirect: 'personCenter/smsLogin',
      component: resolve => require(['@/views/personCenter.vue'], resolve),
      meta: {
        title: '个人中心'
      },
      children: [
        ...loginRegister
      ]
    },
    {
      path: '*',
      name: 'not-found',
      meta: {
        keepAlive: true,
        title: 'notFound'
      },
      component: resolve => require(['@/views/NotFound.vue'], resolve)
    },
    ...product
  ]
})
